body {
  margin: 0;
  background: #1c1c1c;
}

.header-section .body--menu-open {
  overflow: hidden;
}
.header-section .site-header {
  position: fixed;
  left: 0;
  right: 0;
  z-index: 100;
  transition: background-color 0.3s ease, backdrop-filter 0.3s ease;
}
.header-section .site-header--scrolled {
  background: rgba(0, 0, 0, 0.2) !important;
  backdrop-filter: blur(10px);
  top: 0;
}
.header-section .site-header__container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 1rem;
  display: grid;
  grid-template-columns: auto 1fr auto;
  align-items: center;
  gap: 2rem;
}
@media (max-width: 768px) {
  .header-section .site-header__container {
    grid-template-columns: auto 1fr auto;
  }
}
.header-section .logo__wrapper {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}
.header-section .logo__shuffle {
  color: #8B5CF6;
  font-size: 1.5rem;
  font-weight: bold;
}
.header-section .logo__store-text {
  color: #FFFFFF;
  font-size: 1.25rem;
  font-weight: bold;
}
@media (max-width: 768px) {
  .header-section .logo {
    order: 2;
  }
}
.header-section .payment__wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.75rem;
}
.header-section .payment__content {
  width: auto;
  margin: auto;
  display: inline-flex;
  align-items: center;
  border: 1px solid #762bfb;
  padding: 0.5rem 1.5rem;
  border-radius: 50px;
}
.header-section .payment__text {
  color: #FFFFFF;
  font-size: 0.875rem;
  font-family: monospace;
  padding: 0 5px;
}
.header-section .payment__icon {
  width: 24px;
  height: 24px;
}
@media (max-width: 768px) {
  .header-section .payment {
    display: none;
  }
}
.header-section .nav {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.header-section .nav__menu {
  display: flex;
  gap: 0.5rem;
}
@media (max-width: 768px) {
  .header-section .nav__menu {
    display: none;
  }
}
.header-section .nav__link {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 0.875rem;
  transition: color 0.2s ease;
  font-family: system-ui;
}
.header-section .nav__link:hover {
  color: #8B5CF6;
}
.header-section .nav__cart {
  color: #FFFFFF;
  background: #762bfb;
  cursor: pointer;
  transition: color 0.2s ease;
  align-items: center;
  justify-content: center;
  display: flex;
  border-radius: 10px;
  width: 25px;
  height: 26px;
  padding: 0.5rem 1rem;
}
@media (max-width: 768px) {
  .header-section .nav {
    order: 3;
  }
  .header-section .nav__cart {
    margin-left: auto;
  }
}
.header-section .hamburger {
  display: none;
  flex-direction: column;
  justify-content: space-between;
  width: 24px;
  height: 20px;
  background: none;
  border: none;
  cursor: pointer;
  padding: 0;
  z-index: 110;
}
.header-section .hamburger__line {
  width: 100%;
  height: 2px;
  background-color: #762bfb;
  transition: all 0.3s ease;
}
.header-section .hamburger--active .hamburger__line:nth-child(1) {
  transform: translateY(9px) rotate(45deg);
}
.header-section .hamburger--active .hamburger__line:nth-child(2) {
  opacity: 0;
}
.header-section .hamburger--active .hamburger__line:nth-child(3) {
  transform: translateY(-9px) rotate(-45deg);
}
@media (max-width: 768px) {
  .header-section .hamburger {
    display: flex;
    order: 1;
  }
}
.header-section .mobile-menu {
  display: none;
  position: fixed;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.9490196078);
  backdrop-filter: blur(10px);
  z-index: 100;
  transition: left 0.3s ease;
}
.header-section .mobile-menu--active {
  left: 0;
}
.header-section .mobile-menu__content {
  padding: 80px 2rem 2rem;
  height: 100%;
  overflow-y: auto;
}
.header-section .mobile-menu__nav {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin: 2rem;
}
.header-section .mobile-menu__link {
  color: #FFFFFF;
  font-size: 0.875rem;
  text-decoration: none;
  padding: 0.5rem 0;
  transition: color 0.2s ease;
  font-family: system-ui;
}
.header-section .mobile-menu__link:hover {
  color: #762bfb;
}
@media (max-width: 768px) {
  .header-section .mobile-menu {
    display: block;
  }
}

.topbar {
  width: 100%;
  overflow: hidden;
  background: #762bfb;
  padding: 5px 0;
  white-space: nowrap;
  position: relative;
}
.topbar .marquee {
  display: flex;
  width: 100%;
  overflow: hidden;
}
.topbar .marquee .marquee-content {
  font-family: monospace;
  display: flex;
  white-space: nowrap;
  font-size: 14px;
  font-weight: bold;
  text-transform: uppercase;
  color: #FFFFFF;
  align-items: center;
  animation: marquee-scroll 10s linear infinite;
}
.topbar .marquee .marquee-icon {
  color: #997df3;
  padding: 0 0.5rem;
  margin: auto;
  text-align: center;
}

.hero {
  position: relative;
  width: 100%;
  overflow: hidden;
  background: #1c1c1c;
  padding-top: 6rem;
}
.hero__container {
  position: relative;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
  height: auto;
  display: flex;
}
@media (max-width: 768px) {
  .hero__container {
    padding: 0 10px;
  }
}
.hero__image {
  width: 100%;
  aspect-ratio: 16/9;
  object-fit: cover;
  display: block;
  border-radius: 10px;
  position: relative;
}
@media (max-width: 1024px) {
  .hero__image {
    height: 50vh;
  }
}
.hero__content {
  position: absolute;
  left: 54%;
  bottom: 2rem;
  transform: translateX(-50%);
  z-index: 2;
  width: 100%;
  text-align: left;
}
@media (max-width: 1024px) {
  .hero__content {
    bottom: 1.5rem;
  }
}
.hero__button {
  display: inline-block;
  padding: 1rem 2rem;
  background-color: #762bfb;
  color: white;
  text-decoration: none;
  border-radius: 0.25rem;
  font-family: system-ui;
  font-weight: 600;
  font-size: 0.875rem;
  text-transform: uppercase;
  transition: background-color 0.3s ease;
  border: none;
  cursor: pointer;
}
.hero__button:hover {
  background-color: #8b5cf6;
}
@media (max-width: 1024px) {
  .hero__button {
    padding: 0.75rem 1.5rem;
    font-size: 0.75rem;
  }
}
.hero .marquee {
  display: flex;
  width: 100%;
  overflow: hidden;
  background-color: #1c1c1c;
  padding: 1rem 0;
}
.hero .marquee .marquee-content {
  font-family: fantasy;
  display: flex;
  white-space: nowrap;
  font-size: 40px;
  text-transform: uppercase;
  color: #FFFFFF;
  align-items: center;
  animation: marquee-scroll 10s linear infinite;
}
.hero .marquee .marquee-icon {
  color: #997df3;
  padding: 0 10px;
  margin: auto;
  font-size: 54px;
}

@keyframes marquee-scroll {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(-10%);
  }
}
.featured-products {
  padding: 2rem 1rem 1rem;
  background: #1c1c1c;
  color: #fff;
}
.featured-products__container {
  max-width: 1200px;
  margin: 0 auto;
}
.featured-products .product-list {
  display: grid;
}
@media (min-width: 1025px) {
  .featured-products .product-list {
    gap: 1.5rem;
    grid-template-columns: repeat(4, 1fr);
  }
}
@media (max-width: 1024px) {
  .featured-products .product-list {
    overflow-x: auto;
    scroll-snap-type: x mandatory;
    grid-template-columns: repeat(10, 80%);
    grid-gap: 1rem;
    padding: 1rem;
    -webkit-overflow-scrolling: touch;
  }
}
.featured-products .product-card {
  background: #1c1c1c;
  border-radius: 8px;
  overflow: hidden;
}
@media (min-width: 1025px) {
  .featured-products .product-card--hidden {
    display: none !important;
  }
}
@media (max-width: 1024px) {
  .featured-products .product-card--hidden {
    display: block !important;
  }
}
.featured-products .product-card__image-container {
  position: relative;
}
.featured-products .product-card__tags {
  position: absolute;
  top: 10px;
  left: 10px;
  z-index: 2;
  display: flex;
  gap: 0.5rem;
  flex-wrap: wrap;
}
.featured-products .product-card__tag {
  background: #997df3;
  color: #FFFFFF;
  padding: 0.25rem 0.75rem;
  border-radius: 4px;
  font-size: 0.75rem;
  text-transform: capitalize;
  font-family: system-ui;
}
.featured-products .product-card__image-wrapper {
  position: relative;
  padding-top: 100%;
  border: 2px solid #a855f7;
  border-radius: 8px;
  overflow: hidden;
  cursor: pointer;
}
.featured-products .product-card__image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}
.featured-products .product-card:hover .product-card__image {
  transform: scale(1.1);
}
.featured-products .product-card__details {
  padding: 1rem;
  display: grid;
  grid-template-columns: 1fr auto;
  gap: 1rem;
  align-items: center;
}
.featured-products .product-card__info {
  display: flex;
  flex-direction: column;
  gap: 0.3rem;
}
.featured-products .product-card__title {
  font-size: 0.8rem;
  color: #FFFFFF;
  margin: 0;
  font-family: system-ui;
}
.featured-products .product-card__price {
  font-size: 0.8rem;
  color: #FFFFFF;
  margin: 0;
  font-family: system-ui;
}
.featured-products .product-card__add-to-cart {
  padding: 0.75rem 0.75rem;
  background: #762bfb;
  color: white;
  border: none;
  border-radius: 50px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  white-space: nowrap;
}
.featured-products .product-card__add-to-cart .cart-icon {
  width: 25px;
  height: 26px;
}
.featured-products .product-card__add-to-cart:hover {
  background: #9333ea;
}
.featured-products__view-all {
  display: flex;
  align-items: center;
  justify-content: center;
  background: #762bfb;
  border: none;
  border-radius: 7px;
  color: #FFFFFF;
  cursor: pointer;
  margin: 1rem auto;
  padding: 0.5rem 1rem;
  font-size: 1.5rem;
  font-family: fantasy;
}
@media (max-width: 1024px) {
  .featured-products__view-all {
    display: none;
  }
}

.collections {
  background: #1c1c1c;
}
@media (min-width: 1025px) {
  .collections {
    padding: 0.1rem 1rem;
  }
}
@media (max-width: 1024px) {
  .collections {
    padding: 2rem 0.5rem;
  }
}
.collections__container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
  height: auto;
}
@media (max-width: 1024px) {
  .collections__container {
    padding: 0 0;
  }
}
.collections__title {
  text-align: center;
  margin-bottom: 2rem;
  font-family: fantasy;
  font-size: 40px;
  text-transform: uppercase;
  color: #FFFFFF;
  letter-spacing: 0.1em;
  justify-content: center;
  display: flex;
  align-items: center;
}
.collections__title .title-icon {
  color: #997df3;
  padding: 0 10px;
  font-size: 54px;
}
.collections__grid {
  display: grid;
  gap: 1.5rem;
}
@media (min-width: 1025px) {
  .collections__grid::before {
    content: "";
    position: absolute;
    margin-top: -9rem;
    left: 0;
    width: 100%;
    height: 500px;
    background: radial-gradient(circle, rgba(115, 46, 243, 0.3098039216) 15%, rgba(115, 46, 243, 0) 26%, transparent 21%);
    filter: blur(7px);
  }
}
@media (max-width: 1024px) {
  .collections__grid::before {
    content: "";
    position: absolute;
    margin-top: -13rem;
    left: 0;
    width: 100%;
    height: 500px;
    background: radial-gradient(circle, rgba(115, 46, 243, 0.3098039216) 15%, rgba(115, 46, 243, 0) 26%, transparent 21%);
    filter: blur(7px);
  }
}
@media (min-width: 1025px) {
  .collections__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1.5rem;
  }
}
@media (max-width: 1024px) {
  .collections__grid {
    grid-template-columns: 1fr;
  }
}
.collections__card {
  position: relative;
  border-radius: 0.5rem;
  overflow: hidden;
  height: 310px;
}
@media (max-width: 1024px) {
  .collections__card {
    height: 400px;
  }
}
@media (min-width: 1025px) {
  .collections__card:nth-child(4n+3) {
    grid-column: 1/-1;
  }
}
.collections__card-image {
  position: relative;
  height: 100%;
  width: 100%;
}
.collections__image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.collections__card-title {
  position: absolute;
  bottom: 2%;
  left: 1rem;
  color: #ffffff;
  font-family: system-ui;
  font-size: 1.5rem;
  font-weight: bold;
  text-align: left;
  z-index: 2;
  width: 100%;
  padding: 1rem;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}
.collections__card::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.3));
  z-index: 1;
}

.footer {
  background-color: #1c1c1c;
  color: #ffffff;
  padding: 40px 0;
}
@media (max-width: 1024px) {
  .footer {
    padding: 15px 0;
  }
}
.footer .footer__container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
  display: grid;
}
@media (min-width: 1025px) {
  .footer .footer__container {
    grid-template-columns: 1fr 2fr 1fr;
    gap: 40px;
  }
}
@media (max-width: 1024px) {
  .footer .footer__container {
    grid-template-columns: 1fr;
    gap: 30px;
    display: flex;
    flex-direction: column;
  }
}
.footer .footer__column--left {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
@media (max-width: 1024px) {
  .footer .footer__column--left {
    order: 3;
    position: relative;
    margin-top: -28%;
  }
}
.footer .footer__column--left .footer__logo {
  display: flex;
  align-items: center;
  gap: 10px;
}
@media (max-width: 1024px) {
  .footer .footer__column--left .footer__logo {
    flex-direction: row;
    justify-content: flex-start;
    margin-bottom: 1rem;
  }
}
.footer .footer__column--left .footer__logo img {
  width: 180px;
  height: 80px;
}
.footer .footer__column--left .footer__logo-text {
  font-family: system-ui;
  font-size: 0.7rem;
  margin: 0;
}
@media (max-width: 1024px) {
  .footer .footer__column--left .footer__logo-text {
    display: inline;
  }
}
.footer .footer__column--left .footer__logo-mail {
  font-family: system-ui;
  font-size: 0.7rem;
  text-decoration: none;
  color: #FFFFFF;
  margin: 0;
}
@media (max-width: 1024px) {
  .footer .footer__column--left .footer__logo-mail {
    display: inline;
  }
}
.footer .footer__nav {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
@media (max-width: 1024px) {
  .footer .footer__nav {
    order: 1;
  }
}
.footer .footer__nav-primary {
  list-style: none;
  padding: 0;
  margin: 0;
  display: grid;
  font-family: system-ui;
}
@media (min-width: 1025px) {
  .footer .footer__nav-primary {
    grid-template-columns: repeat(4, auto);
    gap: 20px;
    text-align: center;
  }
}
@media (max-width: 1024px) {
  .footer .footer__nav-primary {
    grid-template-columns: 1fr;
    gap: 1rem;
    text-align: left;
  }
  .footer .footer__nav-primary li {
    font-size: 1rem;
    font-weight: 500;
  }
}
.footer .footer__nav-secondary {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  margin-top: 20px;
  font-family: system-ui;
  justify-content: center;
  font-size: 0.68rem;
}
@media (min-width: 1025px) {
  .footer .footer__nav-secondary {
    gap: 2rem;
    text-align: center;
  }
}
@media (max-width: 1024px) {
  .footer .footer__nav-secondary {
    flex-direction: column;
    gap: 0.5rem;
    text-align: left;
    margin-top: 1rem;
  }
}
.footer .footer__nav a {
  color: #ffffff;
  text-decoration: none;
  transition: color 0.3s;
}
.footer .footer__nav a:hover {
  border-bottom: 2px solid #8b5cf6;
}
.footer .footer__payment {
  margin-top: 3rem;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
}
@media (max-width: 1024px) {
  .footer .footer__payment {
    align-items: flex-start;
    margin-top: 2rem;
    order: 2;
  }
}
.footer .footer__payment-icons {
  display: flex;
  gap: 10px;
}
@media (max-width: 1024px) {
  .footer .footer__payment-icons {
    margin-top: 0.5rem;
  }
}
.footer .footer__payment .payment-icon {
  width: 24px;
  height: 24px;
  background-color: #ffffff;
  border-radius: 50%;
}
.footer .footer__payment p {
  font-family: system-ui;
  font-size: 0.7rem;
}
.footer .footer__column--right {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-left: 3rem;
}
@media (max-width: 1024px) {
  .footer .footer__column--right {
    padding-left: 0;
    order: 2;
  }
}
.footer .footer__column--right .footer__newsletter {
  font-family: system-ui;
  font-size: 0.7rem;
}
.footer .footer__column--right .footer__form {
  display: inline-flex;
  margin-top: 0.5rem;
}
@media (max-width: 1024px) {
  .footer .footer__column--right .footer__form {
    width: 100%;
  }
}
.footer .footer__column--right .footer__input {
  flex-grow: 1;
  padding: 8px 12px;
  background-color: #232127;
  border: 1px solid #232127;
  color: #ffffff;
  border-radius: 4px 0 0 4px;
  width: 136px;
}
@media (max-width: 1024px) {
  .footer .footer__column--right .footer__input {
    width: 100%;
  }
}
.footer .footer__column--right .footer__submit {
  background-color: #232127;
  border: none;
  padding: 8px;
  border-radius: 0 4px 4px 0;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}
.footer .footer__column--right .footer__submit-icon {
  width: 20px;
  height: 20px;
  fill: #762bfb;
}
.footer .footer__column--right .footer__social {
  display: flex;
  gap: 20px;
  font-family: system-ui;
  font-size: 0.68rem;
}
@media (max-width: 1024px) {
  .footer .footer__column--right .footer__social {
    display: inline-grid;
    margin-top: 2rem;
    gap: 1rem;
    align-items: center;
    justify-items: end;
  }
}
.footer .footer__column--right .footer__social-link {
  color: #ffffff;
  text-decoration: none;
  transition: color 0.3s;
}
.footer .footer__column--right .footer__social-link:hover {
  color: #8b5cf6;
}